:root {
    --black: #333;
    --white: #eee;
    --violet: rgba(208, 100, 208, 0.6);
    --indigo: rgba(45, 0, 100, 0.6);
    --blue: rgba(0, 0, 235, 0.6);
    --green: rgba(0, 98, 0, 0.6);
    --yellow: rgba(225, 225, 0, 0.6);
    --orange: rgba(225, 135, 0, 0.6);
    --red: rgba(225, 0, 0, 0.6);
    --gold: goldenrod;
    --primary-color: var(--black);
    --primar-bgcolor: #eee;
    --accent-color: #5e00ff;
    --accent-bgcolor: #ff6f00;
    --default-length: .5vh;
    --default-width: .5vw;
    --default-min-length: .1vh;
    --default-min-width: .1vw;
    --default-border: 1px solid var(--accent-bgcolor);
    --default-margin: calc(.2 * var(--default-length));
    --default-padding: calc(.2 * var(--default-length));

    --muted-text-color: #6c757d;

    --muted-red: #dc3545;
    --muted-red-dark: #c82333;
    --muted-green: #28a745;
    --muted-green-dark: #218838;
    --muted-yellow: #fcc830;
    --muted-yellow-dark: #d39e00;
    --muted-orange: #e9a11f;
    --muted-orange-dark: #c18108;
    --muted-blue: #007bff;
    --muted-blue-dark: #0062cc;
    --muted-black: #343a40;
    --muted-black-dark: #23272b;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 98vw;
    min-height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* hacks */
nav.nav.nav-tabs {
    margin: 1rem 0;
}

a.nav-item.nav-link.active {
    border-bottom: thick solid var(--accent-bgcolor);
}

.bg {
    min-height: 900px;
    background-attachment: fixed;
    background-size: cover;
    background-position: center center;
}

.bg.bg-projects {
    background-image: url('./data/images/projects.jpg');
}

.bg.bg-tools {
    background-image: url('./data/images/tools.jpg');
}

@keyframes gradient {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}

@keyframes anti-gradient {
    0% {
        color: rgba(255, 255, 255, 0.5);
    }
    50% {
        color: rgba(0, 0, 0, 0.5);
    }
    100% {
        color: rgba(255, 255, 255, 0.5);
    }
}


@media screen and (min-width: 300px) {
    *, *::after, *::before {
        box-sizing: border-box;
    }

    body {
        min-width: 300px;
    }

    div.grid.grid-responsive.grid-landing-area-links {
        grid-template-columns: 1fr;
    }

    table.table.table-responsive.table-github-repo tr {
        margin: 1rem;
    }

    table.table.table-responsive.table-github-repo td {
        display: block;
    }
}

@media screen and (min-width: 640px) {
    *, *::after, *::before {
        box-sizing: border-box;
    }

    body {
        min-width: 640px;
    }

    div.grid.grid-responsive.grid-landing-area-links {
        grid-template-columns: 1fr;
    }

    table.table.table-responsive.table-github-repo tr {
        margin: 1rem;
    }

    table.table.table-responsive.table-github-repo td {
        display: block;
    }
}

@media screen and (min-width: 1024px) {
    *, *::after, *::before {
        box-sizing: border-box;
    }

    body {
        min-width: 1024px;
    }

    div.grid.grid-responsive.grid-landing-area-links {
        grid-template-columns: repeat(3, 1fr);
    }

    table.table.table-responsive.table-github-repo td {
        display: table-cell;
    }
}

@media screen and (min-width: 1500px) {
    *, *::after, *::before {
        box-sizing: border-box;
    }

    body {
        min-width: 1500px;
    }

    div.grid.grid-responsive.grid-landing-area-links {
        grid-template-columns: repeat(4, 1fr);
    }

    table.table.table-responsive.table-github-repo td {
        display: table-cell;
    }
}

@media handheld {
    .btn-top {
        width: 90vw;
        min-height: 2rem;
    }
}
